import { InputForm } from '../../components/input-forms/input-form';
import { useDispatch, useSelector } from 'react-redux';
import { sendRegisterRequest } from '../../services/actions/user';
import { Redirect } from 'react-router-dom';
import { inputReturns } from '../../utils/inputReturns';

export const RegisterPage = () => {
  const dispatch = useDispatch();
  const state = useSelector(state => state.user.registerUserSuccess)
  const submit = (e) => {
    e.preventDefault();
    const { name, email, password, parentElementEmail, parentElementPassword } = inputReturns();
    if([...parentElementPassword.children].length === 2 || [...parentElementEmail.children].length === 2 || name === '' || email === '' || password === '') {
      alert(',   ')
      return
    }
    dispatch(sendRegisterRequest(name, email, password))
   }
  if (state) return <Redirect to='/login' />
  return (
    <InputForm
      title=''
      name={{placeholder: ''}}
      email={{placeholder: 'E-mail'}}
      password={{placeholder: ''}}
      btnName=''
      btnSubmit={submit}
      redirectInfo={
        [
          {
            id: 1,
            question: ' ?', 
            answer: '',
            link: '/login'
          }
        ]
      }
    />
  )
}